import React, { Component } from 'react';
import style from '../../css/question.scss';

class p3 extends Component {
    render() {
        return (
            <article className={style.problem}>
                <h3> true false</h3>
                <section>
                1
                <br/>
                 [2, 3, 6, 3, 8]
                <br/>
                 <span className={style.blue}>false</span>
                <br/>
                <br/>
                2
                <br/>
                 [2, 4, 6, 8, 10]
                <br/>
                 <span className={style.blue}>true</span>
                </section>
            </article>
        );
    }
}

export default p3;


