import React, { Component } from 'react';
import style from '../../css/question.scss';

class p2 extends Component {
    render() {
        return (
            <article className={style.problem}>
                <h3> k 2  k </h3>
                <section>
                1
                <br/>
                 [23, 2, 4, 6, 7], k = 6
                <br/>
                 <span className={style.blue}>true</span>
                <br/>
                 [2, 4]  length  2 sum 6
                <br/>
                <br/>
                2
                <br/>
                 [23, 2, 6, 4, 7], k = 6
                <br/>
                 <span className={style.blue}>true</span>
                <br/>
                 [23, 2, 6, 4, 7]  length  5 sum 42
                <br/>
                <br/>
                <span className={style.gray}>
                    
                    <br/>
                    <ol>
                        <li>10000</li>
                        <li>32</li>
                    </ol>
                </span>
                </section>
            </article>
        );
    }
}

export default p2;


